import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { Table, Tooltip, OverlayTrigger } from 'react-bootstrap'; // 

const CourseList = () => {

    const data = useStaticQuery(graphql`
    query {
      allFile(filter: {extension: {regex: "/(jpg|jpeg|png)/"}, sourceInstanceName: {eq: "images"}}) {
        edges {
          node {
            childImageSharp {
              gatsbyImageData(width: 30, layout: CONSTRAINED)
            }
            base
          }
        }
      }
    }
  `);


  const getImageByName = (name) => {
    const image = data.allFile.edges.find(edge => edge.node.base === name);
    return image ? getImage(image.node) : null;
  };


  const courses = [
    { name: 'Udemy', platform: 'Udemy', href:"https://www.udemy.com/", icon: 'udemy-icon.png', tooltip:"google mail "},
    { name: '60', platform: '', href:"https://sat.cool/classroom/60", icon: 'sat_knowledge_logo.jpeg',tooltip:"google mail " },
    { name: 'JS/CANVAS', platform: 'Hahow', href:"https://hahow.in/courses/586fae97a8aae907000ce721", icon: 'hahow-icon.jpeg',tooltip:"" },    
    { name: '', platform: 'Hahow', href:"https://hahow.in/courses/60939aa60390311a3a030a1e/discussions",icon: 'hahow-icon.jpeg',tooltip:"" },
    { name: 'Node.jsMongoDB ', platform: 'Hahow', href:"https://hahow.in/courses/60aeac37bca91777bf5bb114/discussions?item=60d37e4291602a62f8bfc224",icon: 'hahow-icon.jpeg' ,tooltip:""}
    // ...
  ];

  return (
    <div>
    <h1></h1>
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>#</th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        {courses.map((course, index) => (
          <tr key={index}>
            <td>
              <GatsbyImage image={getImageByName(course.icon)} alt={course.platform} />
            </td>
            <td>
              <OverlayTrigger
                placement="right"
                overlay={<Tooltip id={`tooltip-${index}`}>{course.tooltip}</Tooltip>}
              >
                <a href={course.href} target="_blank" rel="noopener noreferrer">{course.name}</a>
              </OverlayTrigger>
            </td>
            <td>{course.platform}</td>
          </tr>
        ))}
      </tbody>
    </Table>
  </div>
  );
};

export default CourseList;
